'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';
import Image from 'next/image';

export function Navigation() {
  const [scrolled, setScrolled] = useState(false);
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 20);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  // Prevent scrolling when mobile menu is open
  useEffect(() => {
    if (mobileMenuOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = '';
    }
  }, [mobileMenuOpen]);

  return (
    <header className={`fixed top-0 left-0 w-full z-50 transition-all duration-300 ${scrolled || mobileMenuOpen ? 'bg-neutral-950/80 backdrop-blur-md border-b border-white/5 py-4 shadow-lg' : 'bg-transparent py-5'}`}>
      <div className="container mx-auto px-4 flex justify-between items-center relative z-20">
        <Link href="/" className="flex items-center" onClick={() => setMobileMenuOpen(false)}>
          <Image src="/logo.png" alt="Toro Store" width={120} height={60} className="object-contain drop-shadow-lg" priority />
        </Link>
        
        <button 
          className="md:hidden text-2xl w-10 h-10 flex items-center justify-center rounded-full hover:bg-white/10 transition-colors"
          onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
          aria-label="Toggle Menu"
        >
          <i className={`fa-solid ${mobileMenuOpen ? 'fa-xmark text-red-400' : 'fa-bars text-white'} transition-all`}></i>
        </button>

        {/* Desktop Navigation */}
        <nav className="hidden md:flex items-center gap-8">
          <Link href="#produk" className="font-medium text-neutral-300 hover:text-blue-400 transition-colors">Produk</Link>
          <Link href="#harga" className="font-medium text-neutral-300 hover:text-blue-400 transition-colors">Harga</Link>
          <Link href="#cara-order" className="font-medium text-neutral-300 hover:text-blue-400 transition-colors">Cara Order</Link>
        </nav>

        <a href="#checkout" className="hidden md:inline-flex items-center gap-2 px-6 py-2.5 rounded-full border border-blue-500/50 hover:bg-blue-500 hover:border-blue-400 font-semibold transition-all">
          <i className="fa-brands fa-whatsapp lg:text-green-400 text-white"></i> Order Sekarang
        </a>
      </div>

      {/* Mobile Navigation Overlay */}
      <div className={`fixed inset-0 bg-neutral-950 z-10 md:hidden transition-transform duration-300 flex flex-col pt-24 px-6 ${mobileMenuOpen ? 'translate-y-0' : '-translate-y-full'}`}>
         <div className="flex flex-col gap-6 text-xl">
            <Link href="#produk" className="font-medium p-4 border-b border-neutral-800 text-white hover:text-blue-400" onClick={() => setMobileMenuOpen(false)}>
               <i className="fa-solid fa-gift w-8 text-neutral-400"></i> Produk Layanan
            </Link>
            <Link href="#harga" className="font-medium p-4 border-b border-neutral-800 text-white hover:text-blue-400" onClick={() => setMobileMenuOpen(false)}>
               <i className="fa-solid fa-tag w-8 text-neutral-400"></i> Daftar Harga
            </Link>
            <Link href="#cara-order" className="font-medium p-4 border-b border-neutral-800 text-white hover:text-blue-400" onClick={() => setMobileMenuOpen(false)}>
               <i className="fa-solid fa-clipboard-list w-8 text-neutral-400"></i> Tata Cara Pemesanan
            </Link>
         </div>
         
         <div className="mt-8">
           <a href="#checkout" onClick={() => setMobileMenuOpen(false)} className="w-full flex items-center justify-center gap-2 px-6 py-4 rounded-xl bg-blue-500 hover:bg-blue-400 text-neutral-950 font-bold transition-colors shadow-lg shadow-blue-500/20">
             <i className="fa-brands fa-whatsapp text-2xl"></i> Mulai Pemesanan
           </a>
         </div>
      </div>
    </header>
  );
}
